import '@babel/polyfill';
import Scanner from './data/zbar.js';


var localVideo = document.getElementById('local_video');
var canvas = document.querySelector('canvas');
var ctx = canvas.getContext('2d');
var localStream = null;

// 顔認識結果を四角い枠で描画するための座標
var x = []

var base64 = ''

// ローカルビデオをスタートさせる
navigator.mediaDevices.getUserMedia({
        video: true,
        audio: false
    })
    .then(function (stream) { // success
        localStream = stream;
        localVideo.src = window.URL.createObjectURL(localStream);
    }).catch(function (error) { // error
        console.error('mediaDevice.getUserMedia() error:', error);
        return;
    });

(async () => {
    window.scanner = await Scanner({ locateFile: file => './data/' + file });
})();

// カメラ画像キャプチャ
const capture = async () => {
    if (localStream) {
        // ctx.beginPath();
        // ctx.rect(x[0], x[1], x[2], x[3]);
        // ctx.stroke();
        // Draw image onto canvas
        const ctx = canvas.getContext('2d');
        ctx.drawImage(localVideo, 0, 0);
        const scanRes = scanner.scanQrcode(ctx.getImageData(0, 0, localVideo.width, localVideo.height), localVideo.width, localVideo.height);
        if (scanRes.length > 0) {
            console.log('Find Qrcode: ' + scanRes);
        }
    }
}
// capture();
setInterval(capture, 10000);

const loadImage = async src => {
    // Load image
    const imgBlob = await fetch(src).then(resp => resp.blob());
    const img = await createImageBitmap(imgBlob);
    // Make canvas same size as image
    const canvas = document.createElement('canvas');
    canvas.width = img.width;
    canvas.height = img.height;
    // Draw image onto canvas
    const ctx = canvas.getContext('2d');
    ctx.drawImage(img, 0, 0);
    return {
        imageData: ctx.getImageData(0, 0, img.width, img.height),
        width: img.width,
        height: img.height
    }
}

